.new-expense__controls {
     display: flex;
     flex-wrap: wrap;
     gap: 1rem;
     margin-bottom: 1rem;
     text-align: left;
}

.new-expense__control label {
     font-weight: bold;
     margin-bottom: 0.5rem;
     display: block;
}

.new-expense__control input {
     font: inherit;
     padding: 0.5rem;
     border-radius: 6px;
     border: 1px solid #ccc;
     width: 20rem;
     max-width: 100%;
}

.new-expense__actions {
     text-align: right;
}

@media only screen and (max-width: 320px) {

     .new-expense__controls {
          margin-left: 3rem;
          font-size: 1.6rem;
          font-weight: 800;
     }

     .new-expense__control input {

          width: 29rem;

     }

}